* {
  font-family: Avenir, sans-serif;
  word-wrap: break-word;
}

a {
  text-decoration: none;
}

.assetsImage {
  height: 80vh;
  width: 100%;
  border-radius: 16px;
}

.App {
  text-align: center;
}

.App h1 {
  margin-bottom: 4vh;
}

/* Navigation Bar */

form {
  width: auto;
}
button {
  margin: 1vh;
}
.navbar {
  background-color: #d9b5a5;
}
.navbar-default,
.collapsed {
  border-color: white !important;
  background-color: white !important;
}
.navbar-default,
.toggle {
  background-color: white !important;
}
.navbar-brand,
.navbar-nav .nav-link {
  color: white !important;
  margin: 2vh 2vw 2vh 2vw !important;
}
.navbar-brand:hover,
.navbar-nav:hover .nav-link:hover {
  color: #690505 !important;
}
.navbar-light .navbar-nav .nav-link {
  color: white !important;
}
.navbar-light:hover .navbar-nav:hover .nav-link:hover {
  color: #690505 !important;
}
.navbar-light .navbar-brand {
  color: white !important;
}
.navbar-light:hover .navbar-brand:hover {
  color: #690505 !important;
}

/* Home */

.showcase {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #fff;
  padding: 0 20px;
}

.video-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.video-container video {
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
}

.video-container:after {
  content: '';
  z-index: 1;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  background: rgba(0, 0, 0, 0);
}

.video-content {
  z-index: 2;
  border-radius: 16px;
  padding: 2vh;
}

.video-content h1 {
  margin: 2vh;
}

.btn {
  display: inline-block;
  padding: 10px 30px;
  background-color: rgba(255, 255, 255, 0.4) !important;
  color: #fff !important;
  border-radius: 5px;
  border: solid #fff 1px;
  margin-top: 25px;
  opacity: 0.7;
}

.btn:hover {
  transform: scale(1.05);
}

#about {
  padding: 40px;
  text-align: center;
}

#about p {
  font-size: 1.2rem;
  max-width: 600px;
  margin: auto;
}

#about h2 {
  margin: 30px 0;
  color: var(--primary-color);
}

.social a {
  margin: 0 5px;
}

/* Weather */

.weather {
  background-image: url('./assets/land.jpg');
  background-size: cover;
  background-position: bottom;
  height: 100vh;
}

.weather.cold {
  background-image: url('./assets/colder.jpg') ;
  background-size: cover;
  background-position: bottom;
}

.weather.warm {
  background-image: url('./assets/warmer.jpg');
  background-size: cover;
  background-position: bottom;
}

.weather-empty p {
  color: #fff;
  margin: 0;
  font-weight: bold;
}

main {
  height: auto;
  padding: 12vh 2vh 2vh 2vh;
  border-radius: 24px;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.85);
  border-radius: 16px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

  color: #313131;
  font-size: 20px;

  transition: 0.4s ease-out;
}

.search-box .search-bar:hover {
  background-color: rgba(255, 255, 255, 0.6);
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #fff;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather-description {
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

/* Fade in Text */

.fade-in-section {
  opacity: 0;
  transform: translateY(20vh);
  visibility: hidden;
  transition: opacity 0.4s ease, transform 0.6s ease;
  will-change: opacity, visibility;
}

.fade-in-section.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

/* Signing Page */

/* Colors */
/*
pastel grey: #818D92
pastel beige: #ECEBE4
pastel red #C49792
dark blue: #2B303A
pastel green: #586A6A
*/

.wrapper {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url('https://images.pexels.com/photos/2763967/pexels-photo-2763967.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.form-wrapper {
  width: 400px;
  display: flex;
  flex-direction: column;
  padding: 20px 40px;
  border-radius: 10px;
  box-shadow: 0px 10px 50px #555;
  background-color: #ffffff;
}

.forms {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

label {
  font-size: 0.8em;
  margin-bottom: 0.25em;
  color: #222;
  font-weight: lighter;
}

input {
  padding: 10px 10px;
  border-radius: 5px;
  outline: none;
  border: 1px solid #cfcfcf;
}

input::placeholder {
  font-size: 1.2em;
  font-weight: lighter;
  color: #999;
}

input.error {
  border: 1px solid red;
}

.errorMessage {
  color: red;
  font-size: 0.75em;
  display: relative;
}

.firstName {
  margin-right: 1%;
}

.lastName {
  margin-left: 1%;
}

.firstName,
.lastName,
.email,
.password {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.firstName,
.lastName {
  width: 49%;
}

.email,
.password {
  width: 100%;
}

.createAccount {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.createAccount button {
  background-color: #519e8a;
  color: #fff;
  border: 2px solid #fff;
  width: 100%;
  margin-top: 1em;
  padding: 8px 0px;
  font-size: 1em;
  font-weight: lighter;
  letter-spacing: 1px;
  margin-bottom: 0.25em;
}

.createAccount button:hover {
  color: #519e8a;
  background-color: #fff;
  border: 2px solid #519e8a;
}

.createAccount small {
  color: #222;
  font-weight: lighter;
}

.logout {
  font-weight: lighter;
}

/* Media for Smaller Screens */

@media screen and (max-width: 1000px) {
  .App {
    padding: 0;
  }

  .assetsImage {
    height: auto;
  }
}

@media screen and (max-width: 550px) {
  .video-container {
    background: url('./assets/cover.jpg') no-repeat center center/cover !important;
    background-size: 200vh !important;
  }

  .myVideo {
    display: none !important;
  }
}
